import React from "react";
import { useState } from "react";
import s from "./card.module.css";
import { Link } from "react-router-dom";

const Card = ({ good, onAdd }) => {
  const { name, img, artikul, price, cardId } = good;
  const [count, setCount] = useState(1);

  const plus = () => {
    setCount(count + 1);
  };

  const minus = () => {
    setCount(count - 1);
  };

  return (
    <div className={s.card}>
      <img width={133} height={112} src={img} alt={name} />
      <span className={s.artikul}> : {artikul}</span>
      <div>
        <p className={s.title}>{name}</p>
      </div>
      <div className={s.blockQuality}>
        <div className={s.title}> : {count}</div>
        <div className={s.btnMP}>
          <button onClick={plus} className={s.btn}>
            +
          </button>
          <button
            onClick={minus}
            disabled={count > 0 ? false : true}
            className={s.btn}
          >
            -
          </button>
        </div>
      </div>
      <div>
        <p>: {price * count} </p>
      </div>
      <button className={s.btn} onClick={() => onAdd({ ...good, count })}>
          
      </button>

      <Link to={`/toys/${cardId}`}> </Link>
    </div>
  );
};

export default Card;
